import { Tooltip } from '@mui/material';

function Logo() {
  return (
    <Tooltip title="Dr. Herança - Sistema de Cálculo de Inventário" arrow
    style={{
      paddingTop: '20px',
      paddingBottom: '20px',
      paddingLeft: '10px',
      paddingRight: '10px'
    }}
      >
      <img
        alt="Dr. Herança Logo"
        width={'80%'}
        src="./static/images/logo/dr-heranca-logo.png"
      />
    </Tooltip>
  );
}

export default Logo;
